<template>
  <div>
    <v-simple-table>
      <thead>
        <tr>
          <th v-for="header in headers" :key="header">{{ header }}</th>
        </tr>
      </thead>
      <tbody>
        <board-list-item v-for="article in articles" :key="article.articleNo" v-bind="article"></board-list-item>
      </tbody>
    </v-simple-table>
  </div>
</template>

<script>
import { mapMutations } from "vuex";
import { countListSize, listArticle } from "@/api/board";
import BoardListItem from "@/components/board/item/BoardListItem";

export default {
  name: "BoardList",
  data() {
    return {
      headers: ["", "", "", "", ""],
      articles: [],
    };
  },
  components: {
    BoardListItem,
  },
  methods: {
    ...mapMutations("boardStore", ["SET_PAGE_LENGTH"]),
  },
  created() {
    let category = this.$store.state.boardStore.categoryTab;
    let param = {
      category: category == 0 ? "share" : category == 1 ? "local" : category == 2 ? "notice" : "one_on_one",
      pgno: this.$store.state.boardStore.pgno,
      pageSize: this.$store.state.boardStore.pageSize,
      option: this.$store.state.boardStore.option,
      keyword: this.$store.state.boardStore.keyword,
    };
    console.dir(param);
    countListSize(
      param,
      ({ data }) => {
        console.log("count", data);
        let pageLength =
          data / this.$store.state.boardStore.pageSize + (data % this.$store.state.boardStore.pageSize == 0 ? 0 : 1);
        this.SET_PAGE_LENGTH(pageLength);
      },
      (error) => {
        console.log(error);
      }
    );
    listArticle(
      param,
      ({ data }) => {
        console.log("articles", data);
        this.articles = data;
      },
      (error) => {
        console.log(error);
      }
    );
  },
};
</script>

